import React, { Component } from 'react';
import './App.css';


export default class Nav extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      textblock: (<div>About Me</div>),
      textblock_plainText: "About Me",
      textblockcopy: (<div>Work</div>),
      textblockcopy_plainText: "Work",
    };
  }

  // --- Render function for component state 1 of 1 --- 
  onClick_textBlock = (ev) => {
    // Go to screen 'about me'
    this.props.appActions.goToScreen('aboutme', { transitionId: '_default' });
  
  }
  
  
  onClick_textBlockCopy = (ev) => {
    // Go to screen 'work'
    this.props.appActions.goToScreen('work', { transitionId: '_default' });
  
  }
  
  
  render() {
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
  
    const style_textBlock = {
        fontSize: 12.9,
        fontFamily: "'HelveticaNeue-Medium', sans-serif",
        color: 'rgba(0, 0, 0, 0.8500)',
        textAlign: 'left',
        cursor: 'pointer',
     };
    const style_textBlockCopy = {
        fontSize: 12.9,
        fontFamily: "'HelveticaNeue-Medium', sans-serif",
        color: 'rgba(0, 0, 0, 0.8500)',
        textAlign: 'left',
        cursor: 'pointer',
     };
    
    return (
      <div className="Nav" style={baseStyle}>
        <div className="compContent">
          <div className='elTextBlock' style={style_textBlock} onClick={this.onClick_textBlock} >
            <div>{this.state.textblock}</div>
          </div>
          <div className='elTextBlockCopy' style={style_textBlockCopy} onClick={this.onClick_textBlockCopy} >
            <div>{this.state.textblockcopy}</div>
          </div>
        </div>
      </div>
    )
  }
  

}



