import React, { Component } from 'react';
import './App.css';


export default class Blanc extends Component {

  // This component doesn't use any properties

  // --- Render function for component state 1 of 1 --- 
  render() {
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
  
    const style_rectangle = {
        background: 'rgba(0, 0, 0, 1.000)',
        width: '100%',
        height: '100%',
     };
    const style_rectangle_outer = {
        display: 'none',
        backgroundColor: 'rgba(253, 254, 253, 0.0355)',
        pointerEvents: 'none',
     };
    
    return (
      <div className="Blanc" style={baseStyle}>
        <div className="compContent">
          <div className='elRectangle' style={style_rectangle_outer}>
            <div style={style_rectangle} />
          
          </div>
          
        </div>
      </div>
    )
  }
  

}



