import React, { Component } from 'react';
import './App.css';
import img_elV1 from './images/LandingPageScreen_elV1.jpg';
import Logo from './Logo';
import Blanc from './Blanc';
import Nav from './Nav';


export default class LandingPageScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  // --- Render function for component state 1 of 1 --- 
  render() {
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
  
    const style_v1 = {
        backgroundImage: 'url('+img_elV1+')',
        backgroundSize: 'cover',
        backgroundPosition: '50% 50%',
        pointerEvents: 'none',
     };
    const style_columns = {
        height: 'auto',  // This element is in scroll flow
     };
    const style_columns2 = {
        height: 'auto',  // This element is in scroll flow
     };
    
    return (
      <div className="AppScreen LandingPageScreen" style={baseStyle}>
        <div className="background">
          <div className='containerMinHeight elV1' style={style_v1} />
        </div>
        <div className="layoutFlow">
          <div className='hasNestedComps elColumns'>
            <div style={style_columns}>
              <div className="col0">
                <Logo appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} />
              </div>
              <div className="col1">
                <Blanc appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} />
              </div>
            </div>
          
          </div>
          
          <div className='hasNestedComps elColumns2'>
            <div style={style_columns2}>
              <div className="col0">
                <Nav appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} />
              </div>
              <div className="col1">
                <Blanc appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} />
              </div>
            </div>
          
          </div>
          
        </div>
      </div>
    )
  }
  

}



