import React, { Component } from 'react';
import './App.css';
import img_elLogo from './images/Logo_elLogo.png';


export default class Logo extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      textblock: (<div>STEFAN</div>),
      textblock_plainText: "STEFAN",
      textblockcopy: (<div>BURCEA</div>),
      textblockcopy_plainText: "BURCEA",
    };
  }

  // --- Render function for component state 1 of 1 --- 
  onClick_logo = (ev) => {
    // Go to screen 'landing page'
    this.props.appActions.goToScreen('landingpage', { transitionId: '_default' });
  
  }
  
  
  onClick_textBlock = (ev) => {
    // Go to screen 'landing page'
    this.props.appActions.goToScreen('landingpage', { transitionId: '_default' });
  
  }
  
  
  onClick_textBlockCopy = (ev) => {
    // Go to screen 'landing page'
    this.props.appActions.goToScreen('landingpage', { transitionId: '_default' });
  
  }
  
  
  render() {
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
  
    const style_logo = {
        backgroundImage: 'url('+img_elLogo+')',
        backgroundSize: 'cover',
        backgroundPosition: '50% 50%',
        cursor: 'pointer',
     };
    const style_textBlock = {
        fontSize: 15.1,
        fontFamily: "'Roboto-Black', sans-serif",
        color: 'rgba(0, 0, 0, 0.8500)',
        textAlign: 'left',
        cursor: 'pointer',
     };
    const style_textBlockCopy = {
        fontSize: 15.1,
        fontFamily: "'Roboto-Black', sans-serif",
        color: 'rgba(0, 0, 0, 0.8500)',
        textAlign: 'left',
        cursor: 'pointer',
     };
    
    return (
      <div className="Logo" style={baseStyle}>
        <div className="compContent">
          <div className='elLogo' style={style_logo} onClick={this.onClick_logo}  />
          <div className='elTextBlock' style={style_textBlock} onClick={this.onClick_textBlock} >
            <div>{this.state.textblock}</div>
          </div>
          <div className='elTextBlockCopy' style={style_textBlockCopy} onClick={this.onClick_textBlockCopy} >
            <div>{this.state.textblockcopy}</div>
          </div>
        </div>
      </div>
    )
  }
  

}



